import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { Skeleton } from '../Skeleton';

describe('Test Skeleton component', () => {
  it('Check for skeleton presentation div elements', async () => {
    const content = render(
      <div role="presentation">
        <Skeleton height={'6x'} width={'100%'} borderRadius={'4px'}></Skeleton>
      </div>
    );
    const presentationDiv = content.container.querySelectorAll('[role=presentation]')[0];
    const skeletonElement = presentationDiv.children[0] as HTMLElement;
    expect(skeletonElement.style.borderRadius).equals('4px');
  });
  it('Check for default properties of skeleton', async () => {
    const content = render(<Skeleton height={'4x'} />);
    const skeletonElement = content.container.firstElementChild as HTMLElement;
    expect(skeletonElement.style.width).equals('100%');
    expect(skeletonElement.style.borderRadius).equals('0px');
  });
});
